@charset "UTF-8";
/*workarea widths*/
@import 'https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i|Playfair+Display:400,400i,700,700i,900,900i&subset=cyrillic';
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

body {
  font-family: "Open Sans", sans-serif;
  background-color: #fff; }

img {
  border: 0; }

ul,
li {
  list-style: none; }

button, input, textarea {
  border: none;
  outline: none; }

.secret-svg {
  display: none; }

.page-wrapper {
  height: 100%; }

/* helpers */
.h-page-block {
  width: 100%;
  background-size: cover;
  background-position: 50% 50%;
  position: relative; }
  .h-page-block__inner, .page-header__inner {
    margin: 0 auto;
    border: 1px solid black; }
    @media (min-width: 320px) {
      .h-page-block__inner, .page-header__inner {
        width: 85%; } }
    @media (min-width: 1000px) {
      .h-page-block__inner, .page-header__inner {
        width: 960px; } }
    @media (min-width: 1220px) {
      .h-page-block__inner, .page-header__inner {
        width: 1200px; } }

.original-button {
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  color: #FFFFFF;
  font-weight: 500;
  font-size: 18px;
  line-height: 50px;
  padding: 0 35px;
  border-radius: 50px;
  cursor: pointer;
  background: #d04751;
  /* Old browsers */
  background: -moz-linear-gradient(left, #d04751 1%, #f1b933 77%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #d04751 1%, #f1b933 77%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #d04751 1%, #f1b933 77%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d04751', endColorstr='#f1b933',GradientType=1 );
  /* IE6-9 */ }
  .original-button:hover {
    text-decoration: underline; }

.cols {
  position: relative;
  text-align: center;
  font-size: 0; }

.col {
  font-size: 16px;
  min-height: 1px;
  display: -moz-inline-stack;
  display: inline-block;
  vertical-align: top;
  position: relative; }

/*HEADER*/
.page-header {
  position: fixed;
  left: 0;
  right: 0;
  background-color: #000;
  color: #ffffff;
  height: 80px;
  z-index: 100; }
  .page-header__logo {
    width: 50%;
    text-align: left;
    padding: 15px 0; }
  .page-header__menu {
    width: 50%; }

.header-logo__link {
  text-align: left;
  color: #ffffff;
  text-decoration: none; }
  .header-logo__link:hover {
    text-decoration: none; }

.header-logo__symbol {
  text-align: left;
  font-size: 50px;
  line-height: 1;
  font-weight: 900;
  font-family: "Playfair Display", serif; }

.header-logo__text {
  vertical-align: bottom;
  padding-left: 7px; }

.header-logo__title {
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 22px;
  letter-spacing: 3px;
  text-transform: uppercase; }

.header-logo__subtitle {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 18px;
  text-transform: uppercase; }

.header-nav__inner {
  text-align: right; }

.header-nav__item {
  padding-left: 33px; }

.header-nav__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 80px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none; }
  .header-nav__link:hover {
    font-weight: 500;
    color: #bdbdbd;
    text-decoration: underline; }

/* block 1 */
.first-block {
  padding-top: 80px;
  height: 100%;
  background-image: url("../img/block1_bg.jpg"); }
  .first-block-content__title {
    font-family: "Playfair Display", serif;
    color: #ffffff;
    font-weight: normal;
    font-size: 80px;
    text-align: center;
    text-transform: uppercase; }
  .first-block-content__subtitle {
    font-family: "Open Sans", sans-serif;
    text-align: center;
    font-weight: 300;
    font-size: 20px;
    color: #ffffff; }
  .first-block-scroll {
    position: absolute;
    width: 26px;
    height: 42px;
    bottom: 100px;
    left: 50%;
    margin-left: -13px; }
    .first-block-scroll svg {
      width: 25px;
      height: 42px; }

.second-block {
  padding-top: 150px;
  background-image: url("../img/block2_bg.jpg");
  height: 700px; }
  .second-block-content {
    padding-left: 550px;
    padding-right: 40px;
    text-align: left; }
    .second-block-content__title {
      font-family: "Playfair Display", serif;
      text-transform: uppercase;
      font-weight: normal;
      font-size: 40px;
      color: #000;
      margin-bottom: 50px; }
    .second-block-content__text {
      font-family: "Open Sans", sans-serif;
      font-weight: normal;
      font-size: 16px;
      color: #464646;
      margin-bottom: 30px; }

.third-block {
  padding: 50px 0; }

.services__title {
  font-size: 45px;
  font-weight: normal;
  font-family: "Playfair Display", serif;
  color: #000;
  text-align: center; }

.services-list__item {
  width: 50%; }
  .services-list__item:nth-of-type(even) {
    padding-left: 70px; }
  .services-list__item:nth-of-type(odd) {
    padding-right: 70px; }

.service__title {
  font-size: 30px;
  text-align: left;
  font-weight: normal;
  font-family: "Open Sans", sans-serif; }

.service__description {
  font-size: 16px;
  color: #949494;
  text-align: left; }

.service__icon {
  height: 110px;
  width: 100px;
  float: left;
  margin-right: 45px; }
  .service__icon svg {
    max-height: 100%;
    max-width: 100%; }

.fourth-block {
  padding: 50px 0; }

.letters__title {
  font-size: 45px;
  font-weight: normal;
  font-family: "Playfair Display", serif;
  color: #000;
  text-align: center; }

.letters-slider {
  padding: 45px 0; }

.letter {
  text-align: center;
  margin: 0 auto;
  width: 70%; }
  .letter__link {
    display: inline-block;
    border: 7px solid #cccccc; }
    .letter__link:hover {
      border: 7px solid #dbd7d2; }
  .letter__pic {
    width: auto !important;
    max-width: 100%;
    max-height: 300px; }

.case {
  height: 450px;
  padding: 60px 45px 0;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover; }
  .case__title {
    font-family: "Open Sans", sans-serif;
    font-size: 22px;
    font-weight: 500;
    color: #FFFFFF;
    text-align: left;
    padding-bottom: 5px;
    border-bottom: 3px solid #fff; }
  .case__description {
    padding-top: 10px;
    font-family: "Open Sans", sans-serif;
    color: #FFFFFF;
    font-size: 18px;
    height: 240px; }
  .case__subinfo {
    font-family: "Open Sans", sans-serif;
    color: #FFFFFF;
    font-weight: 600;
    line-height: 1;
    font-size: 16px; }

.cases .owl-nav div {
  position: absolute;
  top: 50%;
  margin-top: -23px;
  background-color: #fff;
  width: 45px;
  height: 45px;
  z-index: 1;
  color: #464646;
  text-align: center;
  cursor: pointer; }
  .cases .owl-nav div:hover .fa {
    color: #464646; }
  .cases .owl-nav div .fa {
    color: #949494;
    font-size: 24px;
    line-height: 45px; }

.cases .owl-prev {
  left: 0; }

.cases .owl-next {
  right: 0; }

.sixth-block {
  height: 900px;
  background-image: url("../img/block6_bg.jpg"); }

.team {
  padding: 90px 0 0; }
  .team__title {
    text-align: center;
    font-family: "Playfair Display", serif;
    font-weight: normal;
    color: #FFFFFF;
    font-size: 45px; }
  .team__subtitle {
    font-family: "Open Sans", sans-serif;
    font-weight: 300;
    color: #949494;
    text-align: center; }

.persons {
  width: 800px;
  margin: 0 auto; }
  .persons__item {
    width: 33%; }

.person__pic-holder {
  width: 130px;
  height: 130px;
  margin: 0 auto; }

.person__pic {
  width: 100%;
  height: 100%;
  border-radius: 50%; }

.person__name {
  font-family: "Playfair Display", serif;
  text-align: center;
  color: #FFFFFF;
  font-size: 22px;
  margin-bottom: 20px; }

.person__bio {
  font-family: "Open Sans", sans-serif;
  color: #949494;
  font-size: 14px;
  font-weight: 300;
  padding: 0 7px; }

.map {
  height: 320px;
  /*костылим*/ }
  .map [class*="-ground-pane"] {
    filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='saturate' values='0'/></filter></svg>#grayscale");
    -webkit-filter: grayscale(1);
    filter: grayscale(1);
    filter: gray;
    -webkit-filter: grayscale(100%);
    -moz-filter: grayscale(100%);
    -ms-filter: grayscale(100%);
    -o-filter: grayscale(100%);
    filter: grayscale(100%); }

.eighth {
  height: 345px;
  background-image: url("../img/footer_bg.jpg"); }

.contacts {
  width: 640px;
  margin: 0 auto;
  border-left: 1px solid white;
  border-right: 1px solid white;
  text-align: center;
  color: #FFFFFF; }
  .contacts__city {
    font-size: 24px;
    font-family: "Open Sans", sans-serif;
    text-transform: uppercase;
    font-weight: 500; }
  .contacts__phone-link {
    font-family: "Open Sans", sans-serif;
    font-size: 45px;
    font-weight: 700;
    color: #FFFFFF;
    text-decoration: none; }
    .contacts__phone-link:hover {
      text-decoration: underline; }
  .contacts__person {
    font-family: "Open Sans", sans-serif;
    font-size: 24px;
    text-transform: uppercase; }
  .contacts__mail-link {
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    color: #FFFFFF;
    text-decoration: none; }
    .contacts__mail-link:hover {
      text-decoration: underline; }
  .contacts__social {
    font-size: 30px; }
    .contacts__social-link {
      color: #FFFFFF;
      text-decoration: none; }
      .contacts__social-link:hover {
        color: #949494; }
